<template>
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-uppercase">
            TITLE
          </th>
          <th class="text-center text-uppercase">
            WRITER
          </th>
          <th class="text-center text-uppercase">
            ID
          </th>
          <th class="text-center text-uppercase">
            DELETE
          </th>
          <!-- <th class="text-center text-uppercase">
            Protein (g)
          </th> -->
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="item in noticeList"
          :key="item.id"
        >
          <td>{{ item.title }}</td>
          <td class="text-center">
            {{ item.writer || 'NULL' }}
          </td>
          <td class="text-center">
            {{ item.id }}
          </td>
          <td class="text-center">
            <v-card-text class="py-3 px-4">
              <v-icon
                size="30"
                @click="deleteData(item.id, item.title)"
              >
                {{ icon }}
              </v-icon>
            </v-card-text>
          </td>
          <!-- <td class="text-center">
            {{ item.protein }}
          </td> -->
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</template>

<script>
import { mdiDeleteOutline } from '@mdi/js'

export default {
  setup() {
    const desserts = [
      {
        dessert: 'Frozen Yogurt',
        calories: 159,
        fat: 6,
        carbs: 24,
        protein: 4,
      },
    ]

    return {
      desserts,
    }
  },
  data() {
    return {
      noticeList: [],
      icon: mdiDeleteOutline,
    }
  },
  mounted() {
    this.getNoticeList()
  },
  methods: {
    getNoticeList() {
      // eslint-disable-next-line no-undef
      // axios.defaults.headers.common['Content-Type'] = 'application/x-www-form-urlencoded'
      // eslint-disable-next-line no-undef
      // axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*'

      // console.log(`${this.$window.API_URL}`)
      // eslint-disable-next-line no-undef
      axios.get(`${this.$window.API_URL}notices`).then(res => {
        if (res.status === 200 || res.status === 201) {
          // this.noticeList = res.data
          this.noticeList = res.data
        } else {
          console.log('status not 200')
        }
      }).catch(() => {
        console.log('catch error11')
      })
    },
    deleteData(id, title) {
      const param = {
        data:
        {
          title,
        },
      }
      console.log(param)
      // eslint-disable-next-line no-undef
      axios.delete(`${this.$window.API_URL}notices/${id}`, param)

      // axios.get(`${this.$window.API_URL}notices/${id}`, param)
        .then(res => {
          if (res.status === 200) {
            // eslint-disable-next-line no-alert
            alert('Delete Complete!')
            this.getNoticeList()
          } else {
            // eslint-disable-next-line no-alert
            alert('Delete Fail')
          }
        }).catch(err => {
          // eslint-disable-next-line no-alert
          alert(`Error : ${err}`)
        })
    },
  },
}
</script>
