<template>
  <v-simple-table dark>
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-uppercase text--primary">
            TITLE
          </th>
          <th class="text-center text-uppercase text--primary">
            DATE
          </th>
          <th class="text-center text-uppercase text--primary">
            ID
          </th>
          <th class="text-center text-uppercase text--primary">
            DELETE
          </th>
          <!-- <th class="text-center text-uppercase text--primary">
            Protein (g)
          </th> -->
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="item in boardList"
          :key="item.id"
        >
          <td>{{ item.TITLE }}</td>
          <td class="text-center">
            {{ item.date }}
          </td>
          <td class="text-center">
            {{ item.id }}
          </td>
          <td class="text-center">
            <v-card-text class="py-3 px-4">
              <v-icon
                size="30"
                @click="deleteData(item.id)"
              >
                {{ icon }}
              </v-icon>
            </v-card-text>
          </td>
          <!-- <td class="text-center">
            {{ item.protein }}
          </td> -->
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</template>

<script>
import { mdiDeleteOutline } from '@mdi/js'

export default {
  setup() {
    const desserts = [
      {
        dessert: 'Frozen Yogurt',
        calories: 159,
        fat: 6,
        carbs: 24,
        protein: 4,
      },
      {
        dessert: 'Ice cream sandwich',
        calories: 237,
        fat: 6,
        carbs: 24,
        protein: 4,
      },
    ]

    return {
      desserts,
    }
  },
  data() {
    return {
      boardList: [],
      icon: mdiDeleteOutline,
    }
  },
  mounted() {
    this.getBoardList()
  },
  methods: {
    getBoardList() {
      // eslint-disable-next-line no-undef
      axios.get(`${this.$window.API_URL}boards`).then(res => {
        if (res.status === 200) {
          // eslint-disable-next-line prefer-destructuring
          this.boardList = res.data[0]

          // console.log(ret)
          // console.log(typeof ret)
        } else {
          // eslint-disable-next-line no-alert
          alert('status not 200')
        }
      }).catch(err => {
        // eslint-disable-next-line no-alert
        alert(`${err}`)
      })
    },
    deleteData(id) {
      // eslint-disable-next-line no-undef
      axios.delete(`${this.$window.API_URL}boards/${id}`)
        .then(res => {
          if (res.status === 200) {
            // eslint-disable-next-line no-alert
            alert('Delete Complete!')
            this.getBoardList()
          } else {
            // eslint-disable-next-line no-alert
            alert('Delete Fail')
          }
        }).catch(err => {
          // eslint-disable-next-line no-alert
          alert(`${err}`)
        })
    },
  },
}
</script>
